  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.tetris-game .tetris {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
