  color: #00FFFF;
  font-size: 1.2rem;
  text-align: center;
  margin-bottom: 10px;
  text-shadow: 0 0 5px #00FFFF;
}

.tetris-game button {
  position: relative;
  z-index: 3;
  background-color: transparent;
  border: 2px solid #00FFFF;
