  background-color: #00FFFF;
  color: #000;
  box-shadow: 
    0 0 10px #00FFFF,
    0 0 20px #00FFFF;
}

@keyframes slide {
  0% {
    transform: rotate(-45deg) translateY(0);
  }
  100% {
    transform: rotate(-45deg) translateY(100%);
  }
}

@keyframes glow {
  from {
    text-shadow: 
      0 0 5px #00FFFF,
      0 0 10px #00FFFF,
      0 0 15px #00FFFF;
  }
  to {
    text-shadow: 
      0 0 10px #00FFFF,
      0 0 20px #00FFFF,
      0 0 30px #00FFFF;
  }
}

@keyframes twinkle {
  0% { opacity: 0.5; }
  50% { opacity: 1; }
  100% { opacity: 0.5; }
}

@media (max-width: 768px) {
  .tetris-game .text-container span {
    font-size: 1.5rem;
  }
  
  .tetris-game .game-layout {
    flex-direction: column;
    align-items: center;
  }
  
  .tetris-game .info-panel {
    flex-direction: row;
    justify-content: center;
    width: 100%;
  }
}

.tetris-game .game-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.8);
  z-index: 10;
}

.tetris-game .message-box {
  text-align: center;
  color: #00FFFF;
  padding: 2rem;
  border: 2px solid #00FFFF;
  background: rgba(0, 0, 0, 0.9);
  box-shadow: 
    0 0 10px #00FFFF,
    0 0 20px #00FFFF;
  animation: messageAppear 0.5s ease-out;
}

.tetris-game .message-box h2 {
  font-size: 3rem;
  margin: 0 0 1rem;
  font-family: 'Press Start 2P', cursive;
  text-shadow: 
    0 0 10px #00FFFF,
    0 0 20px #00FFFF;
  animation: glowText 1.5s ease-in-out infinite alternate;
}

.tetris-game .message-box p {
  font-size: 1.5rem;
  margin: 1rem 0;
  animation: blink 1s infinite;
}

.tetris-game .start-overlay .message-box {
  transform-origin: center;
  animation: messageZoomIn 0.5s ease-out;
}

.tetris-game .end-overlay .message-box {
  animation: messageShake 0.5s ease-out;
}

.tetris-game .message-box button {
  margin-top: 1rem;
  padding: 0.8rem 2rem;
  font-size: 1.2rem;
  background: transparent;
  border: 2px solid #00FFFF;
  color: #00FFFF;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tetris-game .message-box button:hover {
  background: #00FFFF;
  color: #000;
  box-shadow: 
    0 0 10px #00FFFF,
    0 0 20px #00FFFF;
}

@keyframes messageZoomIn {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes messageShake {
  0%, 100% { transform: translateX(0); }
  20%, 60% { transform: translateX(-10px); }
  40%, 80% { transform: translateX(10px); }
}

@keyframes glowText {
  from {
    text-shadow: 
      0 0 10px #00FFFF,
      0 0 20px #00FFFF,
      0 0 30px #00FFFF;
  }
  to {
    text-shadow: 
      0 0 20px #00FFFF,
      0 0 30px #00FFFF,
      0 0 40px #00FFFF;
  }
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

@keyframes messageAppear {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue';

// 상수 정의
const BLOCK_SIZE = 30;
const BOARD_WIDTH = 10;
const BOARD_HEIGHT = 20;

// 테트리스 블록 모양 정의
const SHAPES = [
  [[1, 1, 1, 1]],     // I 모양
  [[1, 1], [1, 1]],   // O 모양
  [[1, 1, 1], [0, 1, 0]], // T 모양
  [[1, 1, 1], [1, 0, 0]], // L 모양
  [[1, 1, 1], [0, 0, 1]], // J 모양
  [[1, 1, 0], [0, 1, 1]], // S 모양
  [[0, 1, 1], [1, 1, 0]]  // Z 모양
];

// 반응형 상태 정의
const gameBoard = ref(null);
const ctx = ref(null);
const score = ref(0);
const gameInterval = ref(null);

// 게임 상태
const gameState = ref({
  board: Array(BOARD_HEIGHT).fill().map(() => Array(BOARD_WIDTH).fill(0)),
  currentPiece: null,
  currentPieceX: 0,
  currentPieceY: 0,
  status: 'ready' // 'ready', 'playing', 'gameover'
});

// 다음 블록 상태 추가
const nextPiece = ref(null);
const nextPieceCanvas = ref(null);

// 게임 설정 상수 추가
const INITIAL_SPEED = 1000; // 초기 속도 (1초)
const MIN_SPEED = 100;      // 최소 속도 (0.1초)
const SPEED_FACTOR = 0.9;   // 속도 증가 비율

// 현재 게임 속도 상태 추가
const gameSpeed = ref(INITIAL_SPEED);

// 게임 관련 메서드들
const createPiece = () => {
  if (nextPiece.value === null) {
    nextPiece.value = SHAPES[Math.floor(Math.random() * SHAPES.length)];
  }
  
  gameState.value.currentPiece = nextPiece.value;
  gameState.value.currentPieceX = Math.floor(BOARD_WIDTH / 2) - Math.floor(nextPiece.value[0].length / 2);
  gameState.value.currentPieceY = 0;
  
  // 새로운 다음 블록 생성
  nextPiece.value = SHAPES[Math.floor(Math.random() * SHAPES.length)];
  drawNextPiece();
};

// collision 함수 수정
const collision = (x = gameState.value.currentPieceX, 
                  y = gameState.value.currentPieceY, 
                  piece = gameState.value.currentPiece) => {
  for (let r = 0; r < piece.length; r++) {
    for (let c = 0; c < piece[r].length; c++) {
      if (piece[r][c]) {
        const newX = x + c;
        const newY = y + r;
        
        if (newX < 0 || newX >= BOARD_WIDTH || newY >= BOARD_HEIGHT) {
          return true;
        }
        
        if (newY >= 0 && gameState.value.board[newY][newX]) {
          // 블록이 맨 위에서 충돌하면 게임 오버
          if (newY === 0) {
            gameOver();
            return true;
          }
          return true;
        }
      }
    }
  }
  return false;
};

const getGhostPosition = () => {
  let ghostY = gameState.value.currentPieceY;
  
  while (!collision(gameState.value.currentPieceX, ghostY + 1, gameState.value.currentPiece)) {
    ghostY++;
  }
  
  return ghostY;
};

// drawBoard 함수 수정
const drawBoard = () => {
  if (!ctx.value) return;
  
  // 우주 배경 그리기
  const gradient = ctx.value.createLinearGradient(0, 0, 0, gameBoard.value.height);
  gradient.addColorStop(0, '#0B0B3B');  // 진한 남색
  gradient.addColorStop(1, '#000033');  // 어두운 남색
  ctx.value.fillStyle = gradient;
  ctx.value.fillRect(0, 0, gameBoard.value.width, gameBoard.value.height);
  
  // 별 그리기
  for (let i = 0; i < 50; i++) {
    const x = Math.random() * gameBoard.value.width;
    const y = Math.random() * gameBoard.value.height;
    const radius = Math.random() * 1.5;
    const opacity = Math.random();
    
    ctx.value.beginPath();
    ctx.value.arc(x, y, radius, 0, Math.PI * 2);
    ctx.value.fillStyle = `rgba(255, 255, 255, ${opacity})`;
    ctx.value.fill();
  }
  
  // 기존 블록 그리기
  for (let y = 0; y < BOARD_HEIGHT; y++) {
    for (let x = 0; x < BOARD_WIDTH; x++) {
      if (gameState.value.board[y][x]) {
        ctx.value.fillStyle = '#4169E1';  // 로얄 블루
        ctx.value.fillRect(x * BLOCK_SIZE, y * BLOCK_SIZE, BLOCK_SIZE - 1, BLOCK_SIZE - 1);
        // 블록 내부 광채 효과
        const glow = ctx.value.createRadialGradient(
          x * BLOCK_SIZE + BLOCK_SIZE/2,
          y * BLOCK_SIZE + BLOCK_SIZE/2,
          0,
          x * BLOCK_SIZE + BLOCK_SIZE/2,
          y * BLOCK_SIZE + BLOCK_SIZE/2,
          BLOCK_SIZE/2
        );
        glow.addColorStop(0, 'rgba(255, 255, 255, 0.3)');
        glow.addColorStop(1, 'rgba(255, 255, 255, 0)');
        ctx.value.fillStyle = glow;
        ctx.value.fillRect(x * BLOCK_SIZE, y * BLOCK_SIZE, BLOCK_SIZE - 1, BLOCK_SIZE - 1);
      }
    }
  }
  
  // 그림자 그리기 (약간 더 밝은 색상으로)
  if (gameState.value.currentPiece) {
    // 1000점 미만일 때만 그림자 표시
    if (score.value < 1000) {
      const ghostY = getGhostPosition();
      ctx.value.fillStyle = 'rgba(100, 149, 237, 0.3)';
      for (let y = 0; y < gameState.value.currentPiece.length; y++) {
        for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
          if (gameState.value.currentPiece[y][x]) {
            ctx.value.fillRect(
              (gameState.value.currentPieceX + x) * BLOCK_SIZE,
              (ghostY + y) * BLOCK_SIZE,
              BLOCK_SIZE - 1,
              BLOCK_SIZE - 1
            );
          }
        }
      }
    }

    // 현재 블록 그리기
    ctx.value.fillStyle = '#00FFFF';
    for (let y = 0; y < gameState.value.currentPiece.length; y++) {
      for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
        if (gameState.value.currentPiece[y][x]) {
          ctx.value.fillRect(
            (gameState.value.currentPieceX + x) * BLOCK_SIZE,
            (gameState.value.currentPieceY + y) * BLOCK_SIZE,
            BLOCK_SIZE - 1,
            BLOCK_SIZE - 1
          );
          // 네온 글로우 효과
          ctx.value.shadowColor = '#00FFFF';
          ctx.value.shadowBlur = 10;
          ctx.value.fillRect(
            (gameState.value.currentPieceX + x) * BLOCK_SIZE,
            (gameState.value.currentPieceY + y) * BLOCK_SIZE,
            BLOCK_SIZE - 1,
            BLOCK_SIZE - 1
          );
          ctx.value.shadowBlur = 0;
        }
      }
    }
  }
};

const mergePiece = () => {
  for (let y = 0; y < gameState.value.currentPiece.length; y++) {
    for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
      if (gameState.value.currentPiece[y][x]) {
        gameState.value.board[gameState.value.currentPieceY + y][gameState.value.currentPieceX + x] = 1;
      }
    }
  }
};

// 점수 업데이트 및 속도 조절 함수
const updateScore = (clearedLines) => {
  const previousThousand = Math.floor(score.value / 1000);
  score.value += clearedLines * 100;
  const currentThousand = Math.floor(score.value / 1000);
  
  // 1000점 단위로 속도 증가
  if (currentThousand > previousThousand) {
    // 최소 속도를 넘지 않도록 제한
    const newSpeed = Math.max(
      MIN_SPEED,
      INITIAL_SPEED * Math.pow(SPEED_FACTOR, currentThousand)
    );
    
    // 게임 인터벌 재설정
    if (gameInterval.value) {
      clearInterval(gameInterval.value);
      gameInterval.value = setInterval(gameLoop, newSpeed);
      gameSpeed.value = newSpeed;
    }
  }
};

// checkLines 함수 수정
const checkLines = () => {
  let linesToRemove = [];
  
  // 완성된 라인 찾기
  for (let y = BOARD_HEIGHT - 1; y >= 0; y--) {
    if (gameState.value.board[y].every(cell => cell)) {
      linesToRemove.push(y);
    }
  }
  
  if (linesToRemove.length > 0) {
    // 라인 제거 애니메이션
    let flash = 0;
    const flashInterval = setInterval(() => {
      flash++;
      
      for (const y of linesToRemove) {
        const color = flash % 2 === 0 ? '#FFFFFF' : '#FFFF00';
        for (let x = 0; x < BOARD_WIDTH; x++) {
          ctx.value.fillStyle = color;
          ctx.value.fillRect(
            x * BLOCK_SIZE,
            y * BLOCK_SIZE,
            BLOCK_SIZE - 1,
            BLOCK_SIZE - 1
          );
        }
      }
      
      if (flash >= 4) {
        clearInterval(flashInterval);
        
        // 라인 제거 및 점수 계산
        linesToRemove.forEach(y => {
          gameState.value.board.splice(y, 1);
          gameState.value.board.unshift(Array(BOARD_WIDTH).fill(0));
        });
        
        // 점수 업데이트 및 속도 조절
        updateScore(linesToRemove.length);
        drawBoard();
      }
    }, 100);
  }
};

const hardDrop = () => {
  if (gameState.value.status !== 'playing') return;
  
  while (!collision()) {
    gameState.value.currentPieceY++;
  }
  gameState.value.currentPieceY--;
  mergePiece();
  checkLines();
  createPiece();
  if (collision()) {
    gameOver();
    return;
  }
  drawBoard();
};

// 게임 오버 함수 수정
const gameOver = () => {
  if (gameInterval.value) {
    clearInterval(gameInterval.value);
    gameInterval.value = null;
  }
  gameState.value.status = 'gameover';
  drawBoard(); // 마지막 상태 그리기
};

// gameLoop 함수 수정
const gameLoop = () => {
  if (gameState.value.status !== 'playing') return;
  
  gameState.value.currentPieceY++;
  if (collision()) {
    gameState.value.currentPieceY--;
    mergePiece();
    checkLines();
    createPiece();
    if (collision()) {
      gameOver();
      return;
    }
  }
  drawBoard();
};

// 게임 시작 함수 수정
const startGame = () => {
  gameState.value = {
    board: Array(BOARD_HEIGHT).fill().map(() => Array(BOARD_WIDTH).fill(0)),
    currentPiece: null,
    currentPieceX: 0,
    currentPieceY: 0,
    status: 'playing'
  };
  score.value = 0;
  gameSpeed.value = INITIAL_SPEED;
  nextPiece.value = null;
  createPiece();
  if (gameInterval.value) clearInterval(gameInterval.value);
  gameInterval.value = setInterval(gameLoop, gameSpeed.value);
  drawBoard();
};

// 키 입력 처리 함수 수정
const handleKeydown = (e) => {
  // 스페이스바 스크롤 항상 방지
  if (e.key === ' ') {
    e.preventDefault();
  }
  
  // 게임이 진행 중이 아니면 여기서 종료
  if (gameState.value.status !== 'playing') {
    return;
  }
  
  // 다른 키 입력도 스크롤 방지
  e.preventDefault();
  
  switch (e.key) {
    case 'ArrowLeft':
      gameState.value.currentPieceX--;
      if (collision()) gameState.value.currentPieceX++;
      break;
    case 'ArrowRight':
      gameState.value.currentPieceX++;
      if (collision()) gameState.value.currentPieceX--;
      break;
    case 'ArrowDown':
      gameState.value.currentPieceY++;
      if (collision()) gameState.value.currentPieceY--;
      break;
    case 'ArrowUp': {
      const rotated = gameState.value.currentPiece[0].map((_, i) => 
        gameState.value.currentPiece.map(row => row[i]).reverse()
      );
      const previousPiece = gameState.value.currentPiece;
      gameState.value.currentPiece = rotated;
      if (collision()) gameState.value.currentPiece = previousPiece;
    }
      break;
    case ' ':
      hardDrop();
      break;
  }
  
  drawBoard();
};

// 다음 블록 그리기 함수
function drawNextPiece() {
  if (!nextPieceCanvas.value || !nextPiece.value) return;
  
  const ctx = nextPieceCanvas.value.getContext('2d');
  const blockSize = 25; // 미리보기의 블록 크기
  
  // 캔버스 초기화
  ctx.clearRect(0, 0, nextPieceCanvas.value.width, nextPieceCanvas.value.height);
  
  // 블록 중앙 정렬을 위한 오프셋 계산
  const offsetX = (nextPieceCanvas.value.width - nextPiece.value[0].length * blockSize) / 2;
  const offsetY = (nextPieceCanvas.value.height - nextPiece.value.length * blockSize) / 2;
  
  // 다음 블록 그리기
  ctx.fillStyle = '#00FFFF';
  for (let y = 0; y < nextPiece.value.length; y++) {
    for (let x = 0; x < nextPiece.value[y].length; x++) {
      if (nextPiece.value[y][x]) {
        ctx.fillRect(
          offsetX + x * blockSize,
          offsetY + y * blockSize,
          blockSize - 1,
          blockSize - 1
        );
        
        // 네온 효과
        ctx.shadowColor = '#00FFFF';
        ctx.shadowBlur = 10;
        ctx.fillRect(
          offsetX + x * blockSize,
          offsetY + y * blockSize,
          blockSize - 1,
          blockSize - 1
        );
        ctx.shadowBlur = 0;
      }
    }
  }
}

// 라이프사이클 훅
onMounted(() => {
  ctx.value = gameBoard.value.getContext('2d');
  // 전역 이벤트 리스너로 변경
  window.addEventListener('keydown', handleKeydown, { passive: false });
  gameState.value.status = 'ready';
  drawBoard();
});

onUnmounted(() => {
  if (gameInterval.value) clearInterval(gameInterval.value);
  window.removeEventListener('keydown', handleKeydown);
});
</script>